import React, { useState, useEffect } from 'react';
import UniversityCard from './UniversityCard';
import UniversityDetails from './UniversityDetails';
import 'bootstrap/dist/css/bootstrap.min.css';

const universitiesData = [
  {
    name: 'Assam University',
    location: 'Silchar, Assam',
    logo: 'data:image/jpeg;base64,',
    details: 'Assam University is a collegiate central public university located at Silchar, Assam, India. It was founded in the year 1994 by the provisions of an act enacted by the Parliament of India.',
    website: 'http://www.aus.ac.in/',
  },
  {
    name: 'Gauhati University',
    location: 'Guwahati, Assam',
    details: 'Gauhati University also known as GU, is a collegiate public state university located in Guwahati, Assam, India. It was established on 26 January 1948 under the provisions of an Act enacted by the Assam Legislative Assembly and is the oldest university in Northeast India.',
    logo: 'data:image/jpeg;base64,',
    website: 'https://gauhati.ac.in/', 
  },
  {
    name: 'Dibrugarh University',
    location: 'Dibrugarh, Assam',
    details: 'Dibrugarh University is a collegiate public state university in the Indian state of Assam. It is located at Dibrugarh, Assam, India. It was set up in 1965 under the provisions of the Dibrugarh University Act, 1965 enacted by the Assam Legislative Assembly.',
    logo: 'data:image/jpeg;base64,',
    website: 'https://dibru.ac.in/?home', 
  },
  {
    name: 'Assam Agricultural University',
    location: 'Jorhat, Assam',
    details: 'Assam Agricultural University is an agricultural education state university which was established on 1 April 1969 in Jorhat in the state of Assam, India.',
    logo: 'data:image/jpeg;base64,',
    website: 'https://www.aau.ac.in/', 
  },
  {
    name: 'Cotton University',
    location: 'Guwahati, Assam',
    details: 'Cotton University is a public state university located in Guwahati, Assam, India. It was established in 2017 by the provisions of an Act from the Assam Legislative Assembly which merged Cotton College State University and Cotton College.',
    logo: 'data:image/jpeg;base64,',
    website: 'https://www.cottonuniversity.ac.in/', 
  },
];


function Assam() {
  const [selectedUniversity, setSelectedUniversity] = useState(null);

  const handleCardClick = (university) => {
    setSelectedUniversity(university);
  };

  const handleDetailsClose = () => {
    setSelectedUniversity(null);
  };

  return (
    <div className="App">
      <h1>Assam Universities</h1>
      <div className="university-container">
        {universitiesData.map((university, index) => (
          <UniversityCard
            key={index}
            university={university}
            onClick={handleCardClick}
          />
        ))}
      </div>
      {selectedUniversity && (
        <UniversityDetails
          selectedUniversity={selectedUniversity}
          onClose={handleDetailsClose}
        />
      )}
    </div>
  );
}

export default Assam;
