import React, { useState } from 'react';
import UniversityCard from './UniversityCard';
import UniversityDetails from './UniversityDetails';
import './state.css';

const universitiesData = [
  {
    name: 'Arunachal University of Studies',
    location: 'Namsai, Arunachal Pradesh',
    logo: 'data:image/jpeg;base64,',
    details: 'Arunachal University of Studies is a private university located at Namsai.The Arunachal University of Studies has been established by Govt. of Arunachal Pradesh through an Act of State Legislature as the State University under the sponsorship and management of World Education Mission',
    website: 'https://www.arunachaluniversity.ac.in/',
  },
  {
    name: 'Indira Gandhi Government College',
    location: 'Tezu, Arunachal Pradesh',
    details: 'Indira Gandhi Government College, Tezu, established in 1986, is a general degree college in Tezu, Arunachal Pradesh. It offers undergraduate courses in science, arts and commerce. It is affiliated to Rajiv Gandhi University.',
    logo: 'data:image/jpeg;base64,',
    website: 'https://www.iggc.org.in/', 
  },
];

function ArunachalPradesh() {
  const [selectedUniversity, setSelectedUniversity] = useState(null);

  const handleCardClick = (university) => {
    setSelectedUniversity(university);
  };

  const handleDetailsClose = () => {
    setSelectedUniversity(null);
  };

  return (
    <div className="App">
      <h1>Arunachal Pradesh Universities</h1>
      <div className="university-container">
        {universitiesData.map((university, index) => (
          <UniversityCard
            key={index}
            university={university}
            onClick={handleCardClick}
          />
        ))}
      </div>
      {selectedUniversity && (
        <UniversityDetails
          selectedUniversity={selectedUniversity}
          onClose={handleDetailsClose}
        />
      )}
    </div>
  );
}

export default ArunachalPradesh;

